.buttonDelete {
    width: 150px;
    height: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    background: red;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 3px rgba(0,0,0,0.15);
    background: #e62222;
   }
   
   .buttonDelete, button span {
    transition: 200ms;
   }
   
   .buttonDelete .text {
    -webkit-transform: translateX(35px);
            transform: translateX(35px);
    color: white;
    font-weight: bold;
   }
   
   .buttonDelete .icon {
    position: absolute;
    border-left: 1px solid #c41b1b;
    -webkit-transform: translateX(110px);
            transform: translateX(110px);
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
   }
   
   .buttonDelete svg {
    width: 15px;
    fill: #eee;
   }
   
   .buttonDelete:hover {
    background: #ff3636;
   }
   
   .buttonDelete:hover .text {
    color: transparent;
   }
   
   .buttonDelete:hover .icon {
    width: 150px;
    border-left: none;
    -webkit-transform: translateX(0);
            transform: translateX(0);
   }
   
   .buttonDelete:focus {
    outline: none;
   }
